/** Checks that passed value is object, but not a callable-object (function) */
export const isObject = (obj) => !!obj && typeof obj === 'object';
/** Checks that passed value is an object or function */
export const isObjectLike = (obj) => isObject(obj) || typeof obj === 'function';
/** Checks if the passed value is primitive */
export const isPrimitive = (obj) => obj === null ||
    typeof obj === 'undefined' ||
    typeof obj === 'string' ||
    typeof obj === 'number' ||
    typeof obj === 'boolean' ||
    typeof obj === 'symbol';
/** Checks that passed object is prototype of some class */
export const isPrototype = (obj) => Object.hasOwnProperty.call(obj, 'constructor');
/** Checks that passed object is array-like */
export const isArrayLike = (value) => {
    if (!value || typeof value !== 'object')
        return false;
    if (Array.isArray(value))
        return true;
    if (typeof value.length !== 'number' || value.length < 0)
        return false;
    return !value.length || Object.hasOwnProperty.call(value, value.length - 1);
};
